<template>
  <div>
    <DxFileManager
      :file-system-provider="remoteProvider"
      :on-selected-file-opened="displayImagePopup"
      current-path="Widescreen"
      @file-uploading="updStarted"
      @upload-started="onUploadStarted"
    >
      <DxPermissions
        :create="true"
        :copy="true"
        :move="true"
        :delete="true"
        :rename="true"
        :upload="true"
        :download="true"
      />
      <DxUpload
        :chunk-size="500000"
        :max-file-size="1000000"
        @upload-started="onUploadStarted"
      />
    </DxFileManager>
    <DxPopup
      :close-on-outside-click="true"
      v-model:visible="popupVisible"
      v-model:title="imageItemToDisplay.name"
      max-height="600"
      class="photo-popup-content"
    >
      <img
        :src="imageItemToDisplay.url"
        class="photo-popup-image"
      >
    </DxPopup>
  </div>

  <div class="widget-container">
    <DxFileUploader
      :accept="accept"
      :multiple="multiple"
      :upload-mode="uploadMode"
      @upload-started="onUploadStarted"
      upload-url="https://js.devexpress.com/Demos/NetCore/FileUploader/Upload"
      @value-changed="e => files = e.value"
      :abort-upload="fileUploader_abortUpload"
    />
    <div
      :style="{display: files.length > 0 ? 'block' : 'none'}"
      class="content"
    >
      <div>
        <h4>Selected Files</h4>
        <div
          v-for="(file, index) in files"
          :key="index"
          class="selected-item"
        >
          <span>Name: {{ file.name }}<br></span>
          <span>Size {{ file.size }}<br></span>
          <span>Type {{ file.size }}<br></span>
          <span>Last Modified Date: {{ file.lastModifiedDate }}</span>
        </div>
      </div>
    </div>
  </div>
  <div class="options">
    <div class="caption">Options</div>
    <div class="option">
      <span>File types</span>
      <DxSelectBox
        :data-source="fileTypesSource"
        value-expr="value"
        display-expr="name"
        value="*"
        @value-changed="e => accept = e.value"
      />
    </div>
    <div class="option">
      <span>Upload mode</span>
      <DxSelectBox
        :items="['instantly', 'useButtons']"
        value="instantly"
        @value-changed="e => uploadMode = e.value"
      />
    </div>
    <div class="option">
      <DxCheckBox
        text="Allow multiple files selection"
        @value-changed="e => multiple = e.value"
      />
    </div>
  </div>
  <!--   -->
  <input
    type="file"
    name="imageFile"
    id="imageFile"
    
  />
  <!--  -->
  <input
    type="button"
    @click="change"
    value=""
  />
