<script>
import { ref, toRefs, reactive } from '@vue/reactivity'
import { onMounted, watch } from '@vue/runtime-core'
// import {
//   DxDataGrid,
//   DxScrolling,
//   DxPager,
//   DxPaging,
// } from 'devextreme-vue/data-grid';
// import {
//   DxFileManager,
//   DxUpload
// } from 'devextreme-vue/file-manager';
import { DxFileManager, DxPermissions } from 'devextreme-vue/file-manager';
import { DxPopup } from 'devextreme-vue/popup';
import RemoteFileSystemProvider from 'devextreme/file_management/remote_provider';

import { DxFileUploader } from 'devextreme-vue/file-uploader';

import { DxCheckBox } from 'devextreme-vue/check-box';
import { DxSelectBox } from 'devextreme-vue/select-box';
export default {
  name: 'd-evextreme',
  components: {
    // DxFileManager,
    // DxPermissions,
    // DxPopup,
    DxFileUploader,
    DxSelectBox,
    DxCheckBox,
  },
  setup(props) {
    const popupVisible = ref(false);
    const imageItemToDisplay = ref({});
    const formRefName = ref('tree-view');
    // multiple: false,
    // accept: '*',
    // uploadMode: 'instantly',
    // fileTypesSource: [
    //   { name: 'All types', value: '*' },
    //   { name: 'Images', value: 'image/*' },
    //   { name: 'Videos', value: 'video/*' },
    // ],
    // files: [],
    // const img = ref({});
    const multiple = ref(false);
    const accept = ref('*');
    const uploadMode = ref('useButtons');
    const fileTypesSource = ref([
      { name: 'All types', value: '*' },
      { name: 'Images', value: 'image/*' },
      { name: 'Videos', value: 'video/*' },
    ]);
    const files = ref([]);

    const remoteProvider = new RemoteFileSystemProvider({
      endpointUrl: 'https://js.devexpress.com/Demos/Mvc/api/file-manager-file-system-images',
    });
    onMounted(() => {
      //  console.log('onMounted',); 
    })
    const query = () => {
      console.log('query');
    };
    const displayImagePopup = (e) => {
      imageItemToDisplay.value = {
        name: e.file.name,
        url: e.file.dataItem.url,
      };
      popupVisible.value = true;
    }
    const onUploadStarted = (e) => {
      console.log('', e);
      const oFile = e.file;    //
      const reader = new FileReader(),
        img = new Image();
      ///
      reader.readAsDataURL(oFile);
      reader.onload = () => {        //
        const url = reader.result;
        img.src = url;
      };
      // console.log('img', img);
      img.onload = function (e) {
        init();
        // console.log('onload', e);
      }
      const init = () => {
        const w = img.width, h = img.height;
        const canvas = document.createElement("canvas");
        const ctx = canvas.getContext('2d');
        //3
        canvas.width = w / 3;
        canvas.height = h / 3;
        console.log('init',img);
        //canvas;
        ctx.drawImage(img, 0, 0, w, h, 0, 0, canvas.width, canvas.height);

        const quality = 0.3;        //0.3
        const data = canvas.toDataURL("image/png"); //png
        //
        window.location.href = data;   
        saveFile(data, new Date().getTime() + ".png");
        document.body.appendChild(canvas);
        function saveFile(data, filename) {
          var save_link = document.createElementNS('http://www.w3.org/1999/xhtml', 'a');
          save_link.href = data;
          save_link.download = filename;
          
          var event = document.createEvent('MouseEvents');
          event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
          save_link.dispatchEvent(event);
        }
      }
    }
    const fileUploader_abortUpload = (file, uploadInfo) => {
      console.log('', file, uploadInfo);
    }
    const updStarted = (e) => {
      console.log('', e);
      if (e.destinationDirectory === 'Images') {
        // your code
        console.log('___________-', e);
        e.cancel = true;
      }
      e.cancel = true;
    };
    const onButtonClick = (e) => {
      console.log('', e);
      if (e.destinationDirectory === 'Images') {
        // your code
        console.log('___________-', e);
        e.cancel = true;
      }
      e.cancel = true;
    };
    const change = () => {
      const oFile = document.getElementById('imageFile').files[0];    //
      console.log('', oFile);
      const reader = new FileReader(),
        img = new Image();
      ///
      reader.readAsDataURL(oFile);
      reader.onload = () => {        //
        const url = reader.result;
        img.src = url;
      };
      console.log('img', img);
      img.onload = function (e) {
        init();
        // console.log('onload', e);
      }
      const init = () => {
        const w = img.width, h = img.height;
        const canvas = document.createElement("canvas");
        const ctx = canvas.getContext('2d');
        //3
        canvas.width = w / 3;
        canvas.height = h / 3;
        //canvas;
        ctx.drawImage(img, 0, 0, w, h, 0, 0, canvas.width, canvas.height);

        const quality = 0.3;        //0.3
        const data = canvas.toDataURL("image/png"); //png
        //
        console.log('data', data);
        window.location.href = data;  
        // saveFile(data, new Date().getTime() + ".png");
        document.body.appendChild(canvas);
      }
      //
      function saveFile(data, filename) {
        var save_link = document.createElementNS('http://www.w3.org/1999/xhtml', 'a');
        save_link.href = data;
        save_link.download = filename;

        var event = document.createEvent('MouseEvents');
        event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
        save_link.dispatchEvent(event);
      }
    };

    return {
      query,
      remoteProvider,
      popupVisible,
      imageItemToDisplay,
      displayImagePopup,
      updStarted,
      change,
      onUploadStarted,
      formRefName,
      onButtonClick,
      multiple,
      accept,
      uploadMode,
      fileTypesSource,
      files,
      fileUploader_abortUpload,
    };
  }
}
</script>

